.categories {
	color: #999999;
	display: flex;
	font-size: 14px;
	list-style: none;
	margin: 16px 0;
	padding-inline-start: 0;
	width: 100%;

	&__item span {
		margin: 0 5px;
	}

	&__item:last-child > span {
		font-weight: bold;
	}

	&__item:before {
		content: '> ';
	}
}

@media only screen and (max-width: 768px) {
	.categories {
		display: none;
	}
}
